import axios from "axios";

export const axiosMixin = {
    data() {
        return {
            axeman: {
                updated: '',
                gotFromApi: '',
                sendToApi: '',
                success: false,
                error: ''
            },
            successCode: 200
        };
    },
    
    methods: {
        axiosError(error) {
            console.log(error.response.data[0].code);
            console.log(error.response.data[0].description);
            this.axeman.success = false;
            this.axeman.error = error.response.data[0].description;
        },

        axemanReset() {
            // this.axeman.updated = '';
            this.axeman.gotFromApi = '';
            this.axeman.sendToApi = '';
            this.axeman.success = false;
            this.axeman.error = '';
        },

        axiosResponseReact(respStatus) {
            console.log("Axios response: " + respStatus);
        },

        async axiosGetLastUpdate(myApi, output) {
            var path = myApi + '/0/LastUpdate';

            var result = await axios.get(path)
                .then(resp => {
                    if (resp.status == this.successCode) {
                        output.updated = resp.data.date;
                        this.axeman.success = true;
                        console.log('%c> DB(' + myApi + ') update: ' + output.updated, 'color:coral;');
                    } else {
                        axiosResponseReact(resp.status);
                    }
                    
                })
                .catch(error => {
                    this.axiosError(error);
                }); 
        },

        async axiosGetData(myApi, output) {
            var path =  myApi + "/";
            var result = await axios.get(path)
            .then(resp => {
                if (resp.status == this.successCode) {
                    console.log('%c> Using DB(' + myApi + ')', 'color:coral;');
                    output.gotFromApi = resp.data;
                    this.axeman.success = true;
                } else {
                    axiosResponseReact(resp.status);
                }
                
            })
            .catch(error => {
                this.axiosError(error);
            });
        },
        async axiosGetDataForId(myApi, id, output,) {
            var path =  myApi + "/";
            var result = await axios.get(path, {params: {id: id }})
            .then(resp => {
                if (resp.status == this.successCode) {
                    console.log('%c> Using DB(' + myApi + ')', 'color:coral;');
                    output.gotFromApi = resp.data;
                    this.axeman.success = true;
                } else {
                    axiosResponseReact(resp.status);
                }
                
            })
            .catch(error => {
                this.axiosError(error);
            });
        },

        async axiosGetUpdatedData(myApi, updateSince, output) {
            this.axeman.success = false;
            var path = myApi + "/GetUpdated";
            var requestObject = { "dateTime": updateSince };
            var result = await axios.post(path, requestObject)
            .then(resp =>{
                if (resp.status == this.successCode) {
                    console.log('%c> DB Fetched updated data: ' + myApi, 'color:coral;');
                    // console.log(resp.data)
                    output.gotFromApi = resp.data;
                    this.axeman.success = true;
                } else {
                    axiosResponseReact(resp.status);
                }
            })
            .catch(error => {
                this.axiosError(error);
            });
        },

        async axiosAddData(myApi, input) {
            this.axeman.success = false;
            var path = myApi + "/"
            console.log(input);
            var result = await axios.post(path, input)
            .then(resp => {
                if (resp.status == this.successCode) {
                    console.log("%c> DB added data to: " + path, 'color:coral;');
                    input.id = resp.data.id;
                    this.axeman.success = true;
                    this.axeman.updated = resp.data.updateDate; 
                } else {
                    axiosResponseReact(resp.status);
                }
            })
            .catch(error => {
                this.axiosError(error);
            });
        },

        async axiosDeleteData(myApi) {
            var path = myApi;
            var result = await axios.delete(path)
                .then(resp => {
                    if (resp.status == this.successCode) {
                        console.log("%c> DB deleted: " + myApi, 'color:coral;');
                        this.axeman.success = true;
                        this.axeman.updated = resp.data.updateDate;
                        console.log(this.axeman.updated);
                    } else {
                        axiosResponseReact(resp.status);
                    }
                })
                .catch(error => {
                    this.axiosError(error);
                });
        },

        async axiosUpdateData(myApi, input) {
            var path = myApi;
            var result = await axios.put(path, input)
                .then(resp => {
                    if (resp.status == this.successCode) {
                        console.log("%c> DB updated: " + myApi, 'color:coral;');
                        this.axeman.success = true;
                        this.axeman.updated = resp.data.updateDate;
                    } else {
                        axiosResponseReact(resp.status);
                    }
                })
                .catch(error => {
                    console.log(error);
                    this.axiosError(error);
                });
        }
    }
};