<template>
  <b-modal
      centered
      size="lg"
      :title="this.title"
      v-model="showMe"
      no-close-on-backdrop
      hide-header-close
      content-class="darkModal"
    footer-class="centered"
    >
    <div>
        <div style="background-color: rgba(0,0,0,0.3); padding: 1px 0 0 13px;">
            <b-form @reset="onClear" style="margin: 5px 5px 20px 5px;">
                <b-row align-v="center">
                    <b-col lg="5" xs="12">
                        <b-form-group id="input-group-1" :label="$t('tableHeadings.' + this.dropdown + 'Code')" label-for="newItemCode">
                            <b-form-input name="newItemCode" id="newItemCode" :placeholder="this.$t('placeholders.' + this.dropdown + 'Code')"
                                            :class="{ 'form-control': true, 'is-invalid': errors.has('newItemCode')}"
                                            v-validate="validExcluded" data-vv-as="Item Code"
                                            v-model="newItem.key_Code"
                            >
                            </b-form-input>
                            <span class="validation-error" v-show="errors.has('newItemCode')">{{ errors.first('newItemCode') }}</span>
                        </b-form-group>
                    </b-col>
                    <b-col lg="5" xs="12">
                        <b-form-group id="input-group-2" :label="$t('tableHeadings.' + this.dropdown + 'Desc')" label-for="newItemDescription">
                            <b-form-input name="newItemDescription" id="newItemDescription" :placeholder="this.$t('placeholders.' + this.dropdown + 'Desc')"
                                            :class="{ 'form-control': true, 'is-invalid': errors.has('newItemDescription')}"
                                            v-validate="'required'" data-vv-as="Item Description"
                                            v-model="newItem.keyDesc"
                            >
                            </b-form-input>
                            <span class="validation-error" v-show="errors.has('newItemDescription')">{{ errors.first('newItemDescription') }}</span>
                        </b-form-group>
                    </b-col>
                    <b-col lg="2" xs="12">
                        <b-button @click="onSubmit" variant="primary" >{{$t('common.add')}}</b-button>
                    </b-col>
                </b-row>
            </b-form>
        </div>
        <!-- <p v-for="item in theList" :key="item.code"> {{ item.label }} <b-button class="btn btn-square btn-danger">DEL: {{ item.code }}</b-button></p> -->
        <v-client-table id="dropdownTable" ref="dropdownTable"
                        :data="theList" :columns="columns" :options="options"
                        
        >
        <!-- @row-click="edit" -->
        <div slot="key_Code" slot-scope="{row}" :key="editMode">
            <span v-if="!row.isEditing">{{row.key_Code}}</span>
            <span v-else>
                <b-form-input type="text" name="editItemCode" id="editItemCode"
                            v-model="row.key_Code"
                ></b-form-input>
                <!-- <span class="validation-error" v-show="errors.has('editItem')">{{ errors.first('editItem') }}</span> -->
                <!-- <b-button type="button" variant="primary" class="tinyAF" @click="update(row); row.isEditing = false; editMode = !editMode">{{ $t('common.save') }}</b-button>&nbsp;
                <b-button type="button" variant="secondary" class="tinyAF" @click="row.key_Code = tempItem.key_Code; row.isEditing = false; editMode = !editMode">{{ $t('common.back') }}</b-button> -->
            </span>
        </div>
        <div slot="keyDesc" slot-scope="{row}" :key="editMode">
            <span v-if="!row.isEditing">{{row.keyDesc}}</span>
            <span v-else>
                <b-form-input type="text" name="editItemDesc" id="editItemDesc"
                            v-model="row.keyDesc"
                ></b-form-input>
                <!-- <span class="validation-error" v-show="errors.has('editItem')">{{ errors.first('editItem') }}</span> -->
                
            </span>
        </div>
            <span slot="actions" slot-scope="{row}" :key="editMode">
                <span v-if="!row.isEditing">
                <b-button class="btn btn-square btn-warning smaller"
                    @click="row.isEditing = true; editMode = !editMode; tempItem.key_Code = row.key_Code; tempItem.keyDesc = row.keyDesc"
                >{{ $t('common.edit') }}</b-button>&nbsp;&nbsp;
                <b-button class="btn btn-square btn-danger smaller"
                    @click="deleteDropdownItem(row)" 
                >{{ $t('common.deleteSomething') }}</b-button>
                </span>
                <span v-else>
                    <b-button type="button" variant="primary" class="smaller" @click="update(row); row.isEditing = false; editMode = !editMode">{{ $t('common.save') }}</b-button>&nbsp;&nbsp;
                    <b-button type="button" variant="secondary" class="smaller" @click="row.key_Code = tempItem.key_Code; row.keyDesc = tempItem.keyDesc; row.isEditing = false; editMode = !editMode">{{ $t('common.back') }}</b-button>
                </span>
            </span>
        </v-client-table> 
    </div>
    <div slot="modal-footer">
        <b-button variant="secondary" @click="closeThis" class="closeModalButton">{{ $t('common.close') }}</b-button>
    </div>
    </b-modal>
    
</template>

<script>
import { axiosMixin } from "@/core/axiosMixin";
import { mapGetters } from 'vuex';
import { mapActions } from 'vuex';

export default {
    mixins: [axiosMixin],
    name: "DropdownEditModal",
    props: {
        showMe: Boolean,
        dropdown: String
    },
    data() {
        return {
            newItem: {
                key_Code: '',
                keyDesc: '',
            },
            tempItem: {
                key_Code: '',
                keyDesc: '',
            },
            title: '',
            // placeholder: '',
            theList: [],
            editMode: false,
            
            validExcluded: '',
            columns: ["key_Code", "keyDesc", "actions"],
            options: {
                filterByColumn: true,
                perPage: 10,
                perPageValues: [],
                pagination: { chunk: 10, dropdown: false },
                skin: "table table-striped",
                headings: { key_Code: "Code", keyDesc: "Description", actions: "" },
                filterable: [ "key_Code", "keyDesc" ],
                sortable: [ "key_Code", "keyDesc" ],
                editableColumns:[ "label" ],
                columnsClasses: {
                    label: 'dropdownLabel',
                },
                highlightMatches: true,
                columnsDropdown: false,
            }
        }
        
    },
    computed: {
    ...mapGetters([
      'cacheExists',
      'getCache',
      'cacheItemsCount',
      'cacheLastUpdate',
      'cacheGetRowById'
    ])
  },
    methods: {
        ...mapActions([
            'cacheReset',
            'cacheFill',
            'cacheAddItem',
            'cacheDeleteDDLItem',
            'cacheUpdateDDLItem',
            'cacheUpdateItem',
            'cacheSetUpdatedDate',
            'cacheUpdate'
        ]),
        closeThis() {
            this.onClear();
            this.$emit('closed');
        },
        async updateContent() {
            if (this.dropdown != '') {
            this.title = this.$t('common.edit') + ': '+ this.$t('tableHeadings.' + this.dropdown);
            this.options.headings.key_Code = this.$t('tableHeadings.' + this.dropdown + 'Code');
            this.options.headings.keyDesc = this.$t('tableHeadings.' + this.dropdown + 'Desc');
            // this.placeholder = this.$t('placeholders.' + this.dropdown);
            
                if (!this.cacheExists(this.dropdown)) {
                    var result = await this.axiosGetData(this.dropdown, this.axeman);
                    if (this.axeman.success == true) {
                        // console.log(this.axeman.gotFromApi.model);
                        this.theList = this.axeman.gotFromApi.model;
                        this.cacheFill({name: this.dropdown, data:  this.theList, updateDate: '2019-10-10'});
                    }
                }
                else {
                    this.theList = this.getCache(this.dropdown);
                }
                for(var item of this.theList) {
                    item.isEditing = false; 
                }
                this.refreshValidString();
            }
        },
        // ============================================= ADDING
        onSubmit(e) {
            e.preventDefault();

            let result = this.$validator.validateAll()
                .then(valid => {
                if (valid) {
                    this.addDropdownItem();
                }
                return false;
                })
                .catch(() => { return false; });
        },
        async addDropdownItem() {
            // console.log('Adding: ' + this.newItem);
            var newMappedItem = this.mapDropdownItem(this.newItem);
            // console.log('Adding: ' + newMappedItem);
            var result = await this.axiosAddData(this.dropdown, newMappedItem);
            if (this.axeman.success == true) {
                newMappedItem.rowid = newMappedItem.id;
                Messenger().post( this.$t('tableHeadings.' + this.dropdown) + ' ' +  this.$t('common.extendedBy') + ' ' + newMappedItem.key_Code);
                // ========== ONLY CACHING, NO SERVER WORKLOAD (ASSUMING NO SIMULTANEOUS EDITING)
                this.cacheAddItem({ name: this.dropdown, data: newMappedItem, updateDate: this.axeman.updated});
                //this.cacheAddItem({ name: this.dropdown, data: newItem, updateDate: '2019-10-10'});
                this.refreshValidString();
                this.onClear();
            }
        },
        mapDropdownItem(item) {
            var dropdownItem = {
                key_Code: item.key_Code,
                keyDesc: item.keyDesc,
            }
            return dropdownItem;
        },
        refreshValidString() {
            this.validExcluded = 'excluded:';
            for(var item of this.theList) {
                    this.validExcluded += item.key_Code + ',';
                }
                // this.validExcluded.length--;
                this.validExcluded = this.validExcluded.substring(0, this.validExcluded.length - 1);
                this.validExcluded = 'required|' + this.validExcluded;
                // console.log(this.validExcluded);
        },

        // ============================================= DELETING
        async deleteDropdownItem(row) {
            console.log('Deleting: ' + row.key_Code);

            let path = this.dropdown + '/' + row.rowid;
            // console.log(row.id);
            var result = await this.axiosDeleteData(path);
            if (this.axeman.success) {
                this.cacheDeleteDDLItem({ name: this.dropdown, id: row.rowid, updateDate: this.axeman.updated});
                Messenger().post( this.$t('tableHeadings.' + this.dropdown) + ' ' + this.$t('common.hasBeenShrinked'));
            } else {
                Messenger().post({ message: this.axeman.error, type: 'error' , showCloseButton: true});
            }

            //this.cacheDeleteDDLItem({ name: this.dropdown, label: row.label, updateDate: '2019-10-10'});
            this.refreshValidString();
        },
        onClear() {
            this.newItem.key_Code = '';
            this.newItem.keyDesc = '';
            // this.editMode = false;
            this.$validator.reset();
        },
        // ============================================= EDITING
        edit(row) {
            console.log('Editing: ' + row.key_Code);
            // row.label = 'Spier'
        },
        async update(row) {
            console.log('Edited: ' + row.key_Code);
            
            var mappedRow = row;
            // console.log(mappedRow);
            let path = this.dropdown + '/' + row.rowid;
            var result = await this.axiosUpdateData(path, mappedRow);
            if (this.axeman.success == true) {
                Messenger().post(this.$t('common.edited') + ' ' + this.$t('tableHeadings.' + this.dropdown) + ': ' + mappedRow.key_Code);
                // data.id = this.form.id;
                
                this.cacheUpdateDDLItem({ name: this.dropdown, data: row, updateDate: this.axeman.updated });
                this.refreshValidString();
            } else {
                console.log(this.axeman.error);
                Messenger().post({ message: this.axeman.error, type: "error" });
            }
            //this.cacheUpdateDDLItem({ name: this.dropdown, data: row, updateDate: '2019-10-10'});  
        },
    },
    watch: {
        'dropdown': function() {
            this.updateContent();
        }
    }

}
</script>

<style scoped>
    .myBackground {
        background-color: rgb(22, 54, 70);
        color: white;
        padding-top: 5px;
    }
    #dropdownTable .smaller {
        padding: 5px 7px;
    }
    #dropdownTable .tinyAF {
        padding: 3px 3px;
    }
    .dropdownLabel {
        background-color: red !important;
        color: black;
    }
    .VueTables__filter-placeholder {
        color: red;
    }
</style>